.tabs {
  &__nav {
    @include flex;

    position: relative;
    margin-bottom: rem(50);
    isolation: isolate;
    gap: 10px;

    &::before {
      content: "";
      position: absolute;
      bottom: -25px;
      width: 100%;
      height: 1px;
      background-color: var(--color-gray-300);
    }

    &-btn {
      @include flex-all-center;

      padding: em(10) em(20);
      font-weight: 700;
      border-radius: var(--br);
      border-color: transparent;
      border-width: 1px;
      border-style: solid;
      color: var(--color-gray-600);
      white-space: nowrap;
      transition: color 0.3s linear, border-color 0.3s linear;

      sup {
        position: relative;
        top: -6px;
        margin-left: 4px;
        font-weight: 900;
        font-size: 10px;
        line-height: 1.5;
      }

      &--active {
        border-color: var(--color-green);
        color: var(--color-green);
      }

      &:hover {
        color: var(--color-gray-800);
      }
    }
  }

  &__content {
    position: relative;
  }

  &__panel {
    display: none;
    opacity: 0;
    visibility: hidden;
    transition: visibility 0.3s linear;

    &--active {
      @include flex-sb;
      visibility: visible;
      animation: slide-up 0.9s forwards, fade-in 0.9s forwards;
    }
  }
}
