:root {
  --font-family: "Lato", sans-serif;
  --font-second: "Ubuntu", sans-serif;
  --content-width: 1230px;
  --wrapper-width: 1860px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --color-white: #fff;
  --color-green: #17696a;
  --color-green-100: #03cea4;
  --color-brown: #b58269;
  --color-pink: #e0aea2;
  --color-blue: #5a87fc;
  --color-blue-100: #c0dded;
  --color-gray-100: #f9f9f9;
  --color-gray-200: #f4f5f7;
  --color-gray-300: #e5e8ed;
  --color-gray-400: #d7dadd;
  --color-gray-500: #b3b7bc;
  --color-gray-600: #9a9ca5;
  --color-gray-700: #787a80;
  --color-gray-800: #424551;
  --color-gray-900: #1e212c;
  --color-black: #000;
  --color-red: #ff4242;
  --color-yellow: #fede41;
  --br: 0.25em;
  --grid-gap: 30px;
}

/*
._example {
  @include for-width(-desktop) {
    opacity: 0.5;
  };
}
*/
/* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid var(--color-green);
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable value-keyword-case */ /* stylelint-disable-next-line at-rule-blacklist */
@font-face {
  font-family: "Lato";
  src: url("../fonts/../fonts/Lato-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/../fonts/Lato-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/../fonts/Lato-Black.woff2") format("woff2");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Ubuntu";
  src: url("../fonts/../fonts/Ubuntu-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
.page {
  min-height: 100vh;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: var(--color-gray-900);
  background-color: var(--color-white);
  scroll-behavior: smooth;
  overscroll-behavior-y: none;
  color-scheme: light dark;
}
.page__body {
  min-width: 320px;
  min-height: 100%;
}

.site-container {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
}
.container--wrapper {
  max-width: var(--wrapper-width);
}

.section {
  margin-bottom: clamp(3rem, 2rem + 8.45vw, 11.25rem);
}

.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  margin-right: clamp(1rem, 0.5rem + 2.7vw, 3.75rem);
}
@media (max-width: calc(29.6875em - 0.0625em)) {
  .logo.hidden {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.input-reset {
  border-color: transparent;
  padding: 15px 30px;
  width: 100%;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.6;
  background-color: transparent;
}
.input-reset::placeholder {
  color: var(--color-gray);
}

.checkboxes {
  display: flex;
  gap: 12px;
  margin-bottom: 3.25rem;
  border: none;
  padding: 0;
}
.checkboxes__label {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  cursor: pointer;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  color: var(--color-gray-700);
  background-color: var(--color-white);
  padding: 0.375rem 1rem;
  border-width: 1px;
  border-style: solid;
  border-color: var(--color-gray-400);
  border-radius: var(--br);
  transition-property: color, background-color, border-color;
  transition-duration: 0.3s;
  transition-timing-function: linear;
}
.checkboxes__label:is(:hover, :focus) {
  border-color: var(--color-green);
}
.checkboxes input[type=radio]:checked + .checkboxes__label {
  border-color: var(--color-green);
  color: var(--color-white);
  background-color: var(--color-green);
}

.login__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  transition: 0.3s color linear;
}
.login__btn::before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-60%);
  left: -23px;
  mask-image: url("../img/sprite.svg#profile");
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: cover;
  width: 16px;
  height: 16px;
  background-color: currentColor;
  transition: background-color 0.6s linear;
}
@media (max-width: calc(48em - 0.0625em)) {
  .login__btn {
    font-size: 0;
  }
}
.login__btn:hover {
  color: var(--color-white);
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}
.dis-scroll::before {
  --vh: 100%;
  content: "";
  position: absolute;
  z-index: 95;
  width: 100%;
  height: calc(var(--vh, 1vh) * 100);
  background-color: rgba(30, 33, 44, 0.75);
}

.offers-slider {
  position: relative;
  overflow: hidden;
  width: 100%;
  max-width: 530px;
}
.offers-slider__item {
  font-weight: 900;
  font-size: 12px;
}
.offers-slider__item a {
  position: relative;
  padding-left: 4px;
  font-weight: 400;
  transition: color 0.3s linear;
}
.offers-slider__item a::before {
  content: "";
  position: absolute;
  bottom: -1px;
  width: 100%;
  height: 0.8px;
  background-color: currentcolor;
  transition: background-color 0.3s linear;
}
.offers-slider__item a:hover::before {
  background-color: transparent;
}
.offers-slider .offers-btn {
  opacity: 0;
  transition: opacity 0.3s liner;
}
.offers-slider:hover .offers-btn {
  opacity: 1;
}

.countdown {
  padding-top: clamp(2rem, 1rem + 3.3vw, 5rem);
}
.countdown__list {
  display: flex;
  gap: 24px;
}
.countdown__item {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-weight: 700;
  font-size: 28px;
}
.countdown__label {
  font-weight: 400;
  font-size: 14px;
}

.return__list {
  list-style-type: disc;
}
.return__list li {
  padding-left: 16px;
  position: relative;
}
.return__list li::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 4px;
  height: 4px;
  background-color: var(--color-green);
  border-radius: 50%;
}

.share {
  display: inline-flex;
  align-items: center;
  margin-bottom: 3.75rem;
  gap: 16px;
}

.payment__list {
  display: inline-flex;
  gap: 1.5rem;
}

@keyframes slide-up {
  0% {
    transform: translateY(-5%);
  }
  100% {
    transform: translateY(0%);
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.carousel {
  position: relative;
  width: 390px;
  height: 440px;
}
.carousel__item {
  height: 440px;
  position: absolute;
  inset: 0;
  opacity: 0;
  transition: 200ms opacity ease-in-out;
  transition-delay: 200ms;
}
.carousel__item[data-active] {
  opacity: 1;
  z-index: 1;
  transition-delay: 0ms;
}
.carousel-btn {
  display: inline-flex;
  align-items: center;
  position: absolute;
  isolation: isolate;
  left: 1rem;
  z-index: 2;
  top: 50%;
}
.carousel-btn::before {
  content: "";
  position: absolute;
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: cover;
  background-color: var(--color-gray-800);
  transition: background-color 0.3s linear;
  mask-image: url("../img/sprite.svg#chevron");
  width: 18px;
  height: 18px;
}
.carousel-btn--reverse {
  left: auto;
  right: 1rem;
  rotate: 180deg;
}
.carousel-btn:hover::before {
  background-color: var(--color-white);
}
.carousel .article__img {
  min-height: 440px;
}

.list li {
  position: relative;
  padding-left: 1rem;
}
.list li::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  border-radius: 100vw;
  width: 4px;
  height: 4px;
  background-color: var(--color-green);
  transform: translateY(-50%);
}

.rating__item {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  font-size: 14px;
  color: var(--color-gray-700);
}
.rating__item::before {
  content: "";
  position: absolute;
  left: 10px;
  mask-image: url("../img/sprite.svg#star");
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: cover;
  width: 12px;
  height: 12px;
  background-color: currentcolor;
}
.rating__item:not(:last-child) {
  margin-bottom: 25px;
}
.rating__progress {
  margin: 0 16px 0 30px;
  appearance: none;
  height: 4px;
  width: 365px;
  border: none;
  border-radius: 40px;
  background-color: var(--color-gray-300);
}
.rating__progress--five {
  --bg-color: #03cea4;
}
.rating__progress--four {
  --bg-color: #1fdf58;
}
.rating__progress--three {
  --bg-color: #f2ea2b;
}
.rating__progress--two {
  --bg-color: #f89828;
}
.rating__progress--one {
  --bg-color: #ff4242;
}
.rating__progress[value]::-webkit-progress-bar {
  background-color: var(--color-gray-300);
  border-radius: 40px;
}
.rating__progress[value]::-webkit-progress-value {
  background-color: var(--bg-color, currentcolor);
  border-radius: 40px 0 0 40px;
}
.rating__progress[value]::-moz-progress-bar {
  background-color: var(--bg-color, currentcolor);
  border-radius: 40px 0 0 40px;
}

.vote__list {
  display: flex;
}
.vote__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.vote__item:not(:last-child) {
  margin-right: 2.2rem;
}
.vote__btn {
  position: relative;
  isolation: isolate;
}
.vote__btn::before {
  content: "";
  left: -20px;
  transform: translateY(-50%);
  position: absolute;
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: cover;
  mask-image: url("../img/sprite.svg#like");
  width: 16px;
  height: 16px;
  background-color: currentcolor;
  transition: background-color 0.3s linear;
}
.vote__btn:is(:hover, :active)::before {
  background-color: var(--color-green-100);
}
.vote__btn--dislike::before {
  mask-image: url("../img/sprite.svg#dislike");
}
.vote__btn--dislike:is(:hover, :active)::before {
  background-color: var(--color-red);
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
}
.pagination__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.pagination__btn::before {
  content: "";
  left: 0;
  position: absolute;
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: cover;
  mask-image: url("../img/sprite.svg#arrow");
  width: 16px;
  height: 16px;
  background-color: currentcolor;
  transition: background-color 0.3s linear;
}
.pagination__btn--reverse::before {
  rotate: 180deg;
  right: 0;
}
.pagination__list {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 2rem;
}
.pagination__list li:not(:last-child) {
  margin-right: 20px;
}
.pagination__list a {
  position: relative;
  font-weight: 700;
  transition: color 0.3s linear;
}
.pagination__list a:is(:hover, [aria-current=page]) {
  color: var(--color-green);
}

.title {
  margin-bottom: clamp(2rem, 0.5rem + 2.7vw, 3.75rem);
  font-weight: 900;
  font-size: clamp(1.56rem, 1.3rem + 1.31vw, 2.88rem);
  line-height: 1.3;
}
.title--hero {
  font-size: clamp(2.19rem, 1.73rem + 2.31vw, 4.5rem);
  letter-spacing: 0.015em;
}
.title--banners {
  margin-bottom: 2.5rem;
  font-size: clamp(1.56rem, 1.48rem + 0.44vw, 2rem);
}

h3 {
  font-weight: 700;
  font-size: clamp(1rem, 0.95rem + 0.25vw, 1.25rem);
  line-height: 1.5;
  text-align: center;
}
h3::first-letter {
  text-transform: capitalize;
}

.footer-title {
  margin-bottom: 12px;
  font-size: 16px;
  line-height: 1.6;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: var(--color-white);
  text-align: left;
}

.slogan {
  display: block;
  margin-bottom: 12px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.06em;
}

.header {
  position: sticky;
  z-index: 999;
  top: 0;
  width: 100%;
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 15px;
}
.header__middle {
  padding: 20px 0;
  background-color: var(--color-white);
}
.header__link {
  position: relative;
  font-weight: 900;
  transition: color 0.3s linear;
}
.header__link::before {
  content: "";
  position: absolute;
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: cover;
  mask-image: url("../img/sprite.svg#phone");
  width: 20px;
  height: 20px;
  background-color: currentcolor;
  transition: background-color 0.3s linear;
  display: none;
  top: 50%;
  transform: translateY(-50%);
}
@media (max-width: calc(62em - 0.0625em)) {
  .header__link::before {
    display: flex;
    opacity: 1;
    visibility: visible;
  }
}
.header__link span {
  font-weight: 400;
}
@media (max-width: calc(62em - 0.0625em)) {
  .header__link span {
    display: none;
  }
}
.header__link:is(:hover, :active) {
  color: var(--color-white);
}
.header__link:hover::before {
  background-color: var(--color-white);
}
.header__action {
  display: flex;
}
@media (max-width: calc(29.6875em - 0.0625em)) {
  .header__action.hidden {
    display: none;
    visibility: hidden;
    opacity: 0;
    transition: opacity 7s linear;
  }
}
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__users {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-left: 25px;
}
.header__bottom {
  display: grid;
  place-items: center;
  padding: 10px 0;
  background-color: var(--color-green);
  color: var(--color-white);
  text-align: center;
}

.header-top {
  color: rgba(255, 255, 255, 0.6);
  background-color: var(--color-gray-900);
  font-size: 14px;
}
.header-top__nav {
  padding: 12px 0;
}
.header-top__list {
  display: flex;
  margin-right: auto;
  margin-left: auto;
}
@media (max-width: calc(36em - 0.0625em)) {
  .header-top__list {
    display: none;
    visibility: hidden;
    opacity: 0;
  }
}
.header-top__item:not(:last-child) {
  margin-right: clamp(15px, 2rem, 35px);
}
@media (max-width: calc(62em - 0.0625em)) {
  .header-top__item:not(:last-child) {
    margin-right: 15px;
  }
}
.header-top__link {
  transition: color 0.3s linear;
}
.header-top__link:is(:hover, :active) {
  color: var(--color-white);
}

.hero {
  margin-bottom: 0;
}
.hero__btns {
  display: inline-flex;
  gap: clamp(1rem, 1rem + 0.8vw, 1.5rem);
}
@media (max-width: calc(23.375em - 0.0625em)) {
  .hero__btns {
    flex-direction: column;
  }
}
.hero__pagination {
  counter-reset: pag;
  left: calc((100% - 1230px) / 2) !important;
  bottom: 186px !important;
  display: inline-flex;
  width: fit-content;
}
@media (max-width: calc(75em - 0.0625em)) {
  .hero__pagination {
    left: 15px !important;
    padding-right: 15px;
  }
}
@media (max-width: calc(75em - 0.0625em)) and (max-width: calc(62em - 0.0625em)) {
  .hero__pagination {
    bottom: 80px !important;
  }
}
.hero__pagination .swiper-pagination-bullet {
  display: flex;
  flex-direction: column;
  border-radius: 0;
  width: 178px;
  height: auto;
  text-align: left;
  background-color: transparent;
  opacity: 1;
}
.hero__pagination .swiper-pagination-bullet::before {
  counter-increment: pag;
  content: "0" counter(pag);
  font-family: var(--font-second);
  font-weight: 700;
  font-size: clamp(1.25rem, 1.15rem + 0.5vw, 1.75rem);
  color: var(--color-gray-800);
  opacity: 0.6;
  transition: color 0.3s ease-in-out, opacity 0.3s ease-in-out;
}
.hero__pagination .swiper-pagination-bullet:hover::before {
  opacity: 1;
}
.hero__bar {
  position: relative;
  overflow: hidden;
  border-radius: 100vh;
  width: 100%;
  height: 2px;
  background-color: var(--color-gray-800);
  opacity: 0.6;
}
.hero__bar::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-gray-800);
  opacity: 0.3;
  transform: scaleX(0);
  transform-origin: left;
}

.swiper-pagination-bullet-active::before {
  color: var(--color-gray-900) !important;
  opacity: 1 !important;
}
.swiper-pagination-bullet-active .hero__bar::after {
  background-color: var(--color-black);
  opacity: 1 !important;
  transform: scaleX(1);
  transition: transform var(--hero-slider-speed) ease-in-out;
}

.hero-slider {
  position: relative;
  overflow: hidden;
}
.hero-slider__item {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: var(--color-gray-300);
  min-height: 800px;
}
@media (max-width: calc(62em - 0.0625em)) {
  .hero-slider__item {
    min-height: 500px;
  }
}
.hero-slider__content {
  padding-top: clamp(2.8rem, 0.5rem + 6.61vw, 8.44rem);
}
.hero-slider__next {
  right: 42px;
}
.hero-slider__prev {
  left: 32px;
}
.hero-slider .slider-btn {
  opacity: 0;
}
.hero-slider:hover .slider-btn {
  opacity: 0.6;
}

.gender {
  position: relative;
  padding-bottom: clamp(3rem, 2rem + 8.45vw, 11.25rem);
}
.gender__list {
  position: absolute;
  top: -33%;
  z-index: 5;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: var(--grid-gap);
}
.gender__item {
  position: relative;
}
.gender__item picture {
  overflow: hidden;
  margin-bottom: 15px;
  border-radius: var(--br);
}
.gender__item a::before {
  content: "";
  position: absolute;
  z-index: 5;
  inset: 0;
}
.gender__item img {
  transition: transform 0.3s ease-in-out;
}
.gender__item:is(:hover, :focus) h3 {
  color: var(--color-green);
}
.gender__item:is(:hover, :focus) img {
  transform: scale(1.08);
}

.arrival {
  padding-top: clamp(2rem, 2rem + 8.45vw, 11.25rem);
}
.arrival__title {
  text-align: center;
}
.arrival__text {
  margin-bottom: 3.75rem;
  text-align: center;
  color: var(--color-gray-700);
}
.arrival__text a {
  position: relative;
  color: var(--color-green);
  transition: color 0.3s linear;
}
.arrival__text a::before {
  content: "";
  position: absolute;
  bottom: -2px;
  width: 100%;
  height: 1px;
  background-color: currentcolor;
}
.arrival__text a:hover {
  color: var(--color-green-100);
}
@media (max-width: calc(36em - 0.0625em)) {
  .arrival {
    padding-top: 3.125rem;
  }
}

.arrival-slider {
  position: relative;
  overflow: hidden;
  padding-bottom: clamp(1rem, 0.5rem + 2.7vw, 3.75rem);
}

.swiper-pagination-bullet {
  border-radius: 100vw;
  width: 30px;
  height: 4px;
  background-color: var(--color-gray-500);
  opacity: 1;
}
.swiper-pagination-bullet:not(:last-child) {
